import React, { Component, Fragment } from 'react'
import Form from './Form'
import Swipe from './Swipe'
import Video from './Video'

import $ from 'jquery'

import { Provider } from './context/Context'

class Layout extends Component {
    state = {
        state: false,
        form: {
            firstName: '',
            lastName: '',
            phone: '',
            company: '',
            dept: '',
            job: '',
            email: '',
            scale: '',
            data: '',
        },
        swipeItem: [
            {
                image: './images/product/1.png',
                info: '混合雲可擴充伺服器- IBM Power Systems',
                link: '/#'
            },
            {
                image: './images/product/1.png',
                info: '混合雲可擴充伺服器- IBM Power Systems',
                link: '/#'
            },
            {
                image: './images/product/1.png',
                info: '混合雲可擴充伺服器- IBM Power Systems',
                link: '/#'
            },
            {
                image: './images/product/1.png',
                info: '混合雲可擴充伺服器- IBM Power Systems',
                link: '/#'
            }
        ],
        videoId: '-U-sHTUJRdk',
        videoItem: [
            'RF8dbGVvycY',
            'Mw-bAl2Tlm0',
            'w69h0zCso-o',
            'rDT45Z5Y9e8',
            'cpWRUoEB5VQ',
        ]
    }

    changeVideo = (e) => {
        this.setState({
            videoId: e
        })
    }
    openForm = (e) => {
        this.setState({
            state: !this.state.state
        })
        $(".video__popup").fadeOut();
    }

    render() {
        const { 
            state,
            form,
            swipeItem,
            videoId,
            videoItem,
        } = this.state;
        const contextValue = {
            form,
            swipeItem,
            videoId,
            videoItem,
            changeVideo: this.changeVideo,
            openForm: this.openForm
        }
        return (
            <Fragment>
                <Provider value={contextValue}>
                    { state ? <Form /> : ''}
                    <Swipe />
                    <Video openForm={this.openForm}/>
                </Provider>
            </Fragment>
        )
    }
}

export default Layout;